
@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
 }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
 }
}

@-webkit-keyframes spinBackward {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
 }
}

@keyframes spinBackward {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
 }
}



